@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
.color-picker-container,.palette-container{background-color:rgba(255,255,255,0.8);padding:3rem;margin:1rem 0;border-top:solid 1rem rgba(255,255,255,0.3);border-bottom:solid 1rem rgba(255,255,255,0.3)}.base-color-hsl{display:flex;margin-top:2rem;justify-content:center}.base-color-hsl>*{margin:0 1rem}.palette-info{margin-bottom:2rem}.palette-info .palette{display:flex;justify-content:center}.palette-info button{margin-top:1rem}.swatch{padding:0.5rem;overflow:hidden}.swatch p{font-size:14px;color:black;-webkit-text-fill-color:white;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:black}.swatch a{color:white;-webkit-text-fill-color:black;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:white;cursor:pointer}html,body{margin:0;box-sizing:border-box;padding:0}h1,h2,p,a,button{font-family:'Permanent Marker', sans-serif;font-size:larger}button{background-color:white}.App{text-align:center;margin:6rem 0}

